<template>
    <el-row>
        <el-col>
            <div class="grid-content bg-purple-dark" style="text-align: right">
                <el-button type="primary" @click="openDelete" size="mini">删除</el-button>
                <el-button type="primary" @click="addProfile(true, undefined)" size="mini">添加项目</el-button>
            </div>
        </el-col>
        <el-col>
            <div id="gather-data-form" class="grid-content bg-purple-dark">
                <el-table
                        :data="tableData"
                        align="left"
                        style="width: 100%">
                    <el-table-column type="expand">
                        <template slot-scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                                <el-form-item v-for="(row,index) in props.row.arr" :key="index">
                                    <span>{{'【环境' + (index+1) + '】 : '}}</span>
                                    <el-button type="text" @click="addProfile(undefined,row,props.row)">{{row}}</el-button>
                                </el-form-item>
                            </el-form>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="项目"
                            prop="item">
                    </el-table-column>
                    <el-table-column label="操作" align="right">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="addProfile(false, scope.row)">添加环境</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-col>
        <el-col>
            <div class="grid-content bg-purple-dark">
                <el-dialog
                        :title="title"
                        :visible.sync="dialogVisible"
                        width="90%"
                        :before-close="handleClose">
                    <span>
                        <el-form ref="form" :model="form" label-width="80px">
                          <el-form-item label="项目名称">
                            <el-input v-model="form.pjName" placeholder="请填写项目名称" :disabled="ispjDisabled"></el-input>
                          </el-form-item>
                          <el-form-item label="环境名称">
                            <el-input v-model="form.prName" placeholder="请填写环境名称" :disabled="isprDisabled"></el-input>
                          </el-form-item>
                          <el-form-item label="数据内容">
                            <el-row>
                                <el-col :span="6">
                                    <div class="grid-content bg-purple-dark">
                                        <el-input type="textarea" v-model="form.data" row="18" @blur="onEditJson"></el-input>
                                    </div>
                                </el-col>
                                <el-col :span="18">
                                    <div class="grid-content bg-purple-dark">
                                        <vue-json-editor v-model="editor" :showBtns="false" @json-change="onJsonChange"></vue-json-editor>
                                    </div>
                                </el-col>
                            </el-row>
                          </el-form-item>
                        </el-form>
                    </span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="handleClose">取 消</el-button>
                        <el-button type="primary" @click="handleSubmit">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </el-col>
        <el-col>
            <div class="grid-content bg-purple-dark">
                <el-dialog
                        title="删除"
                        :visible.sync="isShow"
                        width="30%"
                        :before-close="handleDeleteClose">
                    <span>
                        <el-form ref="deleteForm" :model="deleteForm" label-width="80px">
                          <el-form-item label="项目名称">
                            <el-input v-model="deleteForm.pjName" placeholder="请填写项目名称"></el-input>
                          </el-form-item>
                          <el-form-item label="环境名称">
                            <el-input v-model="deleteForm.prName" placeholder="请填写环境名称"></el-input>
                          </el-form-item>
                        </el-form>
                    </span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="handleDeleteClose">取 消</el-button>
                        <el-button type="primary" @click="deleteItem">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </el-col>
    </el-row>
</template>

<script>
    import Service from './service'
    import vueJsonEditor from 'vue-json-editor'
    export default {
      name: 'SystermMagagement',
      components: {
        vueJsonEditor
      },
      data () {
        return {
          tableData: [],
          dialogVisible: false,
          title: '',
          ispjDisabled: false,
          isprDisabled: false,
          editor: {},
          isShow: false,
          form: {
            pjName: '',
            prName: '',
            data: ''
          },
          deleteForm: {
            pjName: '',
            prName: ''
          }
        }
      },
      methods: {
        addProfile (isTrue, val, i) {
          this.dialogVisible = true
          this.title = isTrue === false ? '添加项目' : (isTrue ? '添加环境' : '编辑环境')
          if (isTrue === false) {
            this.form.pjName = val.item
            this.ispjDisabled = true
          } else if (isTrue === undefined) {
            this.form.prName = val
            this.form.pjName = i.item
            this.ispjDisabled = true
            this.isprDisabled = true
            Service.getDetail({pjName: i.item, prName: val}).then(res => {
              this.editor = res.data
            })
          }
        },
        onEditJson () {
          var newData = this.form.data.split('\n').reduce((res, line) => {
            const [key, val] = line.split('=')
            if (key) {
              res[key] = val
            }
            return res
          }, {})
          let data = Object.assign({}, this.editor)
          Object.assign(data, newData)
          this.editor = data
        },
        onJsonChange (val) {
          this.form.data = val
          console.log(val)
        },
        handleSubmit () {
          Service.addProfile({pjName: this.form.pjName, prName: this.form.prName, data: this.editor}).then(res => {
            console.log(res)
          }).finally(() => {
            this.dialogVisible = false
          })
        },
        openDelete () {
          this.isShow = true
        },
        handleDeleteClose () {
          this.deleteForm.pjName = undefined
          this.deleteForm.prName = undefined
          this.isShow = false
        },
        deleteItem () {
          if (!this.deleteForm.pjName && !this.deleteForm.prName) {
            this.$message('至少填写项目名称！')
            return
          }
          Service.deleteItems({pjName: this.deleteForm.pjName, prName: this.deleteForm.prName}).then(res => {
            this.isShow = false
          })
        },
        handleClose () {
          this.form.pjName = undefined
          this.form.prName = undefined
          this.form.data = undefined
          this.editor = {}
          this.dialogVisible = false
          this.ispjDisabled = false
          this.isprDisabled = false
        }
      },
      mounted () {
        Service.getProfile().then((res = {}) => {
          for (let key in res.data) {
            this.tableData.push({item: key, arr: res.data[key]})
          }
        })
      }
    }
</script>

<style scoped>

</style>


